.row-flex {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch; }

._breadcrumb + ._breadcrumb:before {
  content: '';
  height: 30px;
  width: 2px;
  background: #e0e0e0;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%); }

.title__page {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center; }

.card.card-product.card-product-campanhas .card-image, .card.card-product.card-portal .card-image {
  height: 150px;
  background-position: center;
  background-size: cover;
  background-image: linear-gradient(to top, #0c3483 0%, #a2b6df 100%, #6b8cce 100%, #a2b6df 100%); }
  .card.card-product.card-product-campanhas .card-image ._card-image-link, .card.card-product.card-portal .card-image ._card-image-link {
    display: block;
    height: 100%; }

.card.card-product.card-product-campanhas .card-content .card-description, .card.card-product.card-portal .card-content .card-description {
  height: 65px;
  overflow: hidden;
  overflow-y: auto; }

.card.card-formatos .card-header {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .card.card-formatos .card-header .material-icons {
    position: relative;
    top: 4px;
    margin-right: 10px; }
  .card.card-formatos .card-header .right {
    display: inherit;
    justify-content: flex-end; }
    .card.card-formatos .card-header .right .card-title__button {
      margin: 0 15px;
      text-transform: uppercase;
      font-size: 12px;
      letter-spacing: 1px; }

.card.card-peca .card-content {
  padding-bottom: 5px; }

.card.card-peca .card--footer .btn {
  width: 100%;
  margin: 0;
  border-top-right-radius: 0;
  border-top-left-radius: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #272727; }

.card-formatos .row {
  display: flex;
  align-items: center; }
  .card-formatos .row .label-on-left {
    margin: 0;
    padding-left: 20px; }

.main-panel .content {
  width: 100%;
  max-width: 100%; }

.peca-listagem:not(:first-child) .card {
  filter: grayscale(80%); }
